@import url('https://fonts.googleapis.com/css?family=Barlow+Condensed:400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Barlow&display=swap');
#root{
	font-size: 18px;
	nav{
		.overlay,.overlayDesktop li{
			text-transform:uppercase;
			font-weight: 700;
			font-family: $BarlowCondensed;
			a{
				color: $VeryDarkBlue;
				&:hover{
					text-decoration: underline;
				}
				@media screen and (min-width:850px){
					font-size: 16px;
				}
			}
		}
		li:nth-of-type(4) a{
				color:$GrayishBlue;
			}
			a{
				text-decoration: none;
			}
	}
	header section{
		line-height: 1.6;
		span{
			color:$GrayishBlue;
			text-transform: uppercase;
			font-family: $BarlowCondensed;
			letter-spacing: 3px;
			font-size: 15px;
			.new{
				margin:0 15px 0 0;
				display: inline-block;
				width:35px;
				color:white;
				background-color: $VeryDarkBlue;
				padding:6px 6px 6px 8px;
				border-radius:30px;
				text-align: center;
				font-weight: 700;
			}
		}
		h1{
			color:$VeryDarkBlue;
			font-size:35px;
			font-family: $BarlowCondensed;
			font-weight: 700;
			text-transform: uppercase; 
			@media screen and (min-width:1000px){
				font-size: 50px;
				line-height: 1.2;
			}
		}
		p{
			color:$DarkGrayishBlue;
			font-family: $Barlow;
			@media screen and (min-width:1000px){
				width:290px;
			}
		}
		.button{
			margin:40px 0 0 0;
		button{
			text-transform: uppercase;
			color:white;
			font-family: $BarlowCondensed;
			font-weight: 400;
			font-size: 15px;
		}
		button:nth-of-type(2){
			color:$DarkGrayishBlue;
		}
	}
	}
}
