@import '../../App.scss';
nav{
	display:flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	position: relative;
	padding:37px 31px;
	@media screen and (min-width:850px){
		padding:61px 152px;
		.overlay,.menu-wrap{
			display: none;
		}
	}
	img{
		width: 21px;
		@media screen and (min-width:850px){
			width:32px;
		}
	}
	.overlayDesktop{
		display:none;
		@media screen and (min-width:850px){
			display: block;
			ul{
				width:377px;
				display: flex;
				justify-content: space-between;
				li:nth-of-type(4) {
					position: relative;
					width:25%;
					text-align: right;
					svg{
						position: absolute;
						left:0;
						circle{
							fill:$GrayishBlue;
						}
					}
				}
			}
		}
	}
	.overlay{
		position: fixed;
		top:50%;
		left:50%;
		transform: translate(-50%,-50%);
		opacity: 0;
		background-color: white;
		z-index: 1;
		width:80%;
		min-width: 310px;
		text-align: center;
		border-radius: 2px;
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.2);
		ul{
			li{
				padding:25px 120px;
			}
			li:nth-of-type(4){
				border-top: 1px solid $GrayishBlue;
				width:30px;
				display:block;
				margin:0 auto;
				svg{
					display: none;
				}
			}
		}
		transition: all 0.5s ease-out;
		&.active{
			opacity: 1;
		}
	}
}