header{
	padding:0 0 88px 0;
	@media screen and (min-width:1000px){
		position: relative;
		padding:0 0 137px 0;
	}
	img{
		display: block;
		min-width: 500px;
		width:90%;
		max-width: 700px;
		position: relative;
		left:50px;
		@media screen and (min-width:462px){
			left:35%;
		}
		@media screen and (min-width:1000px){
			left:50%;
			top:40px;
		}
	}
	section{
		min-width: 292px;
		margin:70px auto 0 auto;
		width:100%;
		max-width: 500px;
		padding:0 20px;
		@media screen and (min-width:1000px){
			position: absolute;
			top:0;
			left:40px;
			width:45%;
		}
	}
	.button{
		width:100%;
		max-width: 377px;
		display: flex;
		justify-content: space-between;
		button{
			width:49%;
			max-width: 182px;
			height: 47px;
			border-radius: 5px;
			background-color: hsl(0, 100%, 68%);
			border:none;
			&:hover{
				background-color: hsl(0, 100%, 80%)
			}
		}
		button:nth-of-type(2){
			background-color: transparent;
			&:hover{
				border:1px solid black;
			}
		}
	}
}